.demo-line {
  position: fixed;
  bottom: 0;
  left: 0;
  background: chocolate;
  width: 100%;
  height: 4em;
  display: flex;
  align-items: center;
  color: dimgray;

  span {
    color: white;
    margin-left: 1em;
    font-size: 1.3em;
  }
}

.demo-app-frame {
  position: relative;
  height: calc(100% - 4em);
  max-height: 90%;
  overflow: hidden;
}

.demo-indicator {
  background: rebeccapurple;
  position: relative;
  display: inline-block;
  padding: 1em;
  font-weight: bold;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  line-height: 1em;
  text-align: center;
  font-size: 1em;  
  margin-left: 1em;

  &[data-selected="true"] {
    width: 2em;
    height: 2em;
    line-height: 2em;
    
    box-shadow: 0px 0px 0px .3em chocolate;
    color: white;
    transform: translateY(-2em);
  }
}

.quick-info-panel {
  background: white;
  border-radius: .5em;
  width: 10em;
  height: 4em;
  position: absolute;
  top: 3em;
  left: 50%;
  z-index: 500;
  padding: 1em;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;

  &::before {
    content: "";
    width: 1em;
    height: 1em;
    position: absolute;
    top: -.5em;
    left: -.5em;
    background: maroon;
    border-radius: 50%;
  }

}

.quick-next {
  background: indianred;
  color: white;
  height: 2em;  
  line-height: 2em;
  text-align: center;
  width: 40%;
  padding: 0 1em;
  align-self: flex-end;
}