.ar-button {
  position: absolute;
  padding: 1em;
  background: black;
  z-index: 100;
  user-select: none;
}

.ar-setup {
  color: white;
  top: 0;
  left: 0;
}

.ar-info {
  color: white;
  top: 3em;
  left: 0;  
}

.ar-cancel {
  color: white;
  top: 0;
  right: 0;  
}

.ar-model-name {
  color: white;
  top: 0;
  left: 45%;
}

.ar-modal-title {
  font-size: 1.5em;
  text-transform: uppercase;
}

.ar-modal-window {
  position: absolute;
  background: black;
  z-index: 200;
  top: 4em;
  left: 0;
  width: 20em;
  font-size: .7em;
  padding: 1em;
}

.ar-check-box {  
  position: relative;  
  height: 4em;
  user-select: none;

  &:before {
    content: "";
    display: block;
    position: absolute;
    width: 2em;
    height: 1.5em;
    background: #777;
    top: 1.7em;
    left: 0;
  }

  &[data-checked="true"]:before {
    background: white;
  }

  &[data-checked="false"] {
    color: #777;
  }

  span {
    color: #AAA;
  }
}

.--nosel {
  user-select: none;
}