body, html {
  background: #333;
  color: #999;
  font-family: sans-serif;
  font-weight: 400;
  font-size: 18px; 
  margin: 0;
  padding: 0;
  overflow: hidden;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background:#333;  
  width: 100vw;
  height: calc(100vh - 4em);
  overflow: hidden;  
}

.page-layer {
  position: absolute;
  top: 0;
  left: 0;
}

.welcome  {
  position: absolute;
  top: 2em;
  left: 2em;
}

.logo {
  position: absolute;
  bottom: 2em;
  left: 2em;
  width: 50px;
  height: 20px;
  background: #777;
}

.info-box, .start-box, .about-box {
  @include flex-center();
  width: 30%;
  height: 70%;
  text-transform: uppercase;
}

.about-box {  
  p { margin: .1em; color: white; font-weight: 700;}
  p:first-child { font-size: 1.5em; font-weight: 800;}
  p:last-child  { font-size: .8em; font-weight: 100;}
}

.info-box, .about-box {
  flex-direction: column;
  background: #111;
}

.info-box p {
  text-align: center;
}

.start-box {  
  background: gray;
  color: white;
}

.next-button {
  position: absolute;
  bottom: 2em;
  right: 2em;
  background: grey;
  color: white;
  padding: 1em;
}

.modal-window {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 60%;
  background: #EEE;
  z-index: 200;
}

.modal-header {
  @include flex-center();
  background: #000;
  color: white;
  padding: 1em;
}

.modal-content {
  @include flex-center();
  position: relative;
  height: 100%;
  p { 
    color: black; 
    font-size: 2em;
    font-weight: bold;
  }
}

.area-3d-canvas {
  width: 50%;
  height: 50%;
}

.modal-cancel-button {
  text-transform: uppercase;
  background: transparent;
  color: #222;
  height: 0;
  line-height: 0em;
  padding: 1em;
}

.modal-next-button {
  text-transform: uppercase;
  background: #444;
  color: white;
  height: 0;
  line-height: 0em;
  padding: 1em;
}

.--positioning-modal {
  height: 30%;
}

.button-line {
  display: flex;
  justify-content: space-evenly;
  height: 100%;
  align-items: center;
}

.fixing-page {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100vw;
  height: 90vh;
  overflow: hidden;  
  
}

.fixing-button {
  text-transform: uppercase;
  background: #777;
  color: white;
  height: 0;
  line-height: 0em;
  padding: 2em;
  align-self: flex-end; 
}

.center-layer {
  position: absolute;
  top: 0;
  left: 0;
}

.transparent { background: none; }
.eventsNone { pointer-events: none; }
.eventsAuto { pointer-events: auto; }
